.contact {
  padding-top: 5px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 65px;
}

.contact:hover .options,
.contact:hover .mini-options {
  opacity: 1;
}

.profile-pic {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 90px;
  height: 40px;
}

.profile-pic img {
  height: 40px;
  width: 40px;
  border: 1px solid #282828;
  border-radius: 50%;
  object-fit: cover;
  position: relative;
  box-sizing: border-box;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
}

.details {
  height: 40px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.details h3 {
  margin: 0;
  font-family: "Lexend";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #ffffff;
}

.details p {
  margin: 0;
  font-family: "Lexend";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.01em;
  color: rgba(255, 255, 255, 0.56);
}

.options {
  position: absolute;
  right: 0;
  display: flex;
  opacity: 0;
  transition: 0.5s;
  justify-content: space-evenly;
  align-items: center;
  min-width: 100px;
  height: 40px;
}

.options button {
  background-color: transparent;
  color: #ffffff8f;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 5px;
  width: 40px;
  height: 40px;
  font-size: 20px;
  border-radius: 8px;
}

.MuiMenu-list {
  background-color: #1e1e1e;
  color: #fffdfd8f;
}

.MuiMenuItem-root:hover {
  color: #ffffff;
}

#mui-icon {
  margin-right: 10px;
}

.options button :hover {
  background: transparent;
  color: #ffffff;
  font-size: 30px;
  transition: all 0.2s ease-in-out 0s;
}

.mini-options {
  display: none;
  position: absolute;
  opacity: 0;
  right: 0;
  transition: 0.5s;
  height: 40px;
}

.mini-options button :hover {
  background: transparent;
  color: #ffffff;
  font-size: 30px;
  transition: all 0.2s ease-in-out 0s;
}

#resources-miniMenu {
  display: none;
}

@media (max-width: 480px) {
  .options {
    display: none;
  }

  .mini-options {
    display: initial;
  }

  #resources-menu {
    display: none;
  }
  #resources-miniMenu {
    display: initial;
  }
}

.modal{
    width: 100vw;
    height: 100%;
    background: rgba(0,0,0,0.56);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;
}

form{
    margin-right: 30px;
    overflow: hidden;
    padding: 20px;
    width: 360px;
    height: 570px;
    box-shadow:  0 5px 16px rgba(0,0,0,0.2);
    background: #141414;
    color: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    border-radius: 10px;
    z-index: 20;
}

form p{
    font-family: 'Glysa';
    height: 5%;
    width: 100%;
    font-size: 24px;
    font-weight: 400;
}

.profile-img{
    height: 30%;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.img-container{
    width: 88px;
    height: 100%;
    display: flex;
    align-items: center;
}

.img-container img{
    width: 88px;
    height: 88px;
    object-fit: cover;
    border-radius: 50%;
    border: 1px solid #232323;
}

.addImg-btn{
    cursor: pointer;
    font-family: 'Lexend';
    display: flex;
    align-items: center;
    padding:  8px 18px 8px 16px;
    border-radius: 8px;
    font-size: 14px;
    background-color: #2D2D2D;
    border: 1px solid #232323;
    color: #FFFFFF;
    margin: 20px;
}

.delete-btn {
    cursor: pointer;
    font-family: 'Lexend';
    display: flex;
    align-items: center;
    padding:  8px;
    border-radius: 8px;
    font-size: 14px;
    background-color: #2D2D2D;
    border: 1px solid #232323;
    color: #FFFFFF;
}



.items{
    font-family: 'Lexend';
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.item{
    height: 33%;
    font-size: 14px;
    font-weight: 400;
    display: flex;
    flex-direction: column;
    color: rgba(255, 255, 255, 0.56)
}

.item input{
    margin-top: 5px;
    background-color: #1E1E1E;;
    padding: 12px;
    border: 1px solid #373737;
    color: #FFFFFF;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 400;
}

.buttons-container{
    height: 15%;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.buttons-container button{
    background-color: #2D2D2D;
    padding: 12px;
    color: #FFFFFF;
    border: none;
    border-radius: 10px;
    margin: 5px;
}


.fileBase{
    overflow: hidden;
    display: flex;
    flex-direction: row;
}

::-webkit-file-upload-button{
    cursor: pointer;
}

input[type="file"]{
    font-size: 30px;
    width: 160px;
    position: absolute;
    top: 37%;
    left: 30%;
    opacity: 0;
}

@media (max-width: 480px) {
    form{
        width: 260px;
        height: 370px;
    }
    form p{
        font-size: 16px;
    }
    
    .img-container{
        width: 48px;
    }
    
    .img-container img{
        width:48px;
        height: 48px;
    }

    .addImg-btn{
        font-size: 10px;
    }
    
.item{
padding: 5px;
}

.item input{
    padding: 8px;
    font-size: 12px;
}

.buttons-container button{
    padding: 8px;
}
input[type="file"]{
    width: 135px;
    top: 30%;
    left: 25%;
}

}

.App{
    border: 15px solid #1E1E1E;
    display: flex;
    justify-self: center;
    width: 100vw;
    height: 100%;
    min-height: 100vh;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: hidden;
}

.light {
    -webkit-filter: invert(1);
            filter: invert(1);
}
.light img {
    -webkit-filter: invert(1);
            filter: invert(1);
}

.dark {
    background-color: #141414;
    color: #FFFFFF;
}

.left, .right{
    width: 35%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.middle {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.cell{
    border: 2px solid #1E1E1E;
}

.sm{
    display: flex;
    height: 10%;
}

.lg{
    height: 80%;
}

.nav{
    display: flex;
    height: 10%;
    justify-content: space-between;
}

.title{
    color: #FFFFFF;
    align-self: center;
    padding: 25px;
}

.icons{
    font-size: 28px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    min-width: 250px;
}


.arrow{
    font-size: 28px;
    padding-right: 10px;
    align-items: center;
    justify-content: flex-end;
}

.sun {
    font-size: 28px;
    padding-left: 10px;
    align-items: center;
    justify-content: flex-start;
}

.profile{
    width: 35px;
    height: 35px;
    background-color: #262626;
    border-radius: 50%;
    border: 1px solid transparent;}

img{
    width: 35px;
    height: 35px;
    object-fit: contain;
    position: absolute;
    border-radius: 50%;
}

.MuiSvgIcon-root{
    color: #FFFFFF;
}

.plus-btn{
    cursor: pointer;
    background: transparent;
    color: #FFFFFF;
    font-size: 28px;
    font-weight: 400;
    border: none;
    display: none;
}

.add-btn{
    margin: 0 25px 0 20px;
    cursor: pointer;
    background: linear-gradient(180deg, #282828b3 0%, #282828 100%), #141414;
    background-image: linear-gradient(180deg, #282828 0%, #3C3C3C 100%);
    color: #FFFFFF;
    font-style: normal;
    font-family: 'Lexend';
    letter-spacing: 0.01em;
    line-height: 20px;
    padding: 8px 16px 8px 12px;
    font-size: 14px;
    font-weight: 400;
    border-radius: 1000px;
    border: 1px solid #3C3C3C;
    transition: 0.8s;
}

.add-btn:hover {
    background: rgba(255, 255, 255, 0.1);
    transition: ease-out;
}


.add-btn span {
    padding-right: 10px;
    font-size: 26px;
    font-weight: 500;
}

#resources-settingMenu {
    display: none;

}


@media (max-width: 769px){
    .left, .right, .sm{
        display: none;
    }
    .lg{
        height: 90%;
    }
    #resources-settingMenu {
        display: initial;
    
    }
}

@media (max-width: 480px) {
    .title{
        font-size: 12px;
    }
    .profile , .profile img{
        width: 30px;
        height: 30px;
    }
    .add-btn{
        display: none;
    }
    .plus-btn{
        display: initial;
    }
    .icons{
        min-width: 200px;
    }


}
